'use server';
 
import { z } from 'zod';
import db from '../lib/db';
import { EventSchema, EventState } from '../lib/definitions';
import { auth } from '../auth';
import { revalidatePath } from 'next/cache';
import { redirect } from 'next/navigation';

export async function createEvent(prevState: EventState, formData: FormData) {
  const session = await auth();
  if (session?.user?.role !== 'ADMIN') {
    return { message: 'Unauthorized' };
  }

  const validatedFields = EventSchema.safeParse({
    title: formData.get('title'),
    description: formData.get('description'),
    date: formData.get('date'),
    location: formData.get('location'),
    imageUrl: formData.get('imageUrl'),
  });
 
  if (!validatedFields.success) {
    return {
      errors: validatedFields.error.flatten().fieldErrors,
      message: 'Missing Fields. Failed to Create Event.',
    };
  }
 
  const { title, description, date, location, imageUrl } = validatedFields.data;
 
  try {
    await db.event.create({
      data: {
        title,
        description,
        date: new Date(date),
        location,
        imageUrl,
      },
    });
  } catch (error) {
    return {
      message: 'Database Error: Failed to Create Event.',
    };
  }
 
  revalidatePath('/admin/events');
  revalidatePath('/network'); // Assuming events are shown here
  redirect('/admin/events');
}

export async function deleteEvent(id: string) {
  const session = await auth();
  if (session?.user?.role !== 'ADMIN') {
    return { message: 'Unauthorized' };
  }

  try {
    await db.event.delete({ where: { id } });
    revalidatePath('/admin/events');
    return { message: 'Deleted Event.' };
  } catch (error) {
    return { message: 'Database Error: Failed to Delete Event.' };
  }
}

export async function getEvents() {
  try {
    const events = await db.event.findMany({
      orderBy: { date: 'desc' },
    });
    return events;
  } catch (error) {
    console.error('Failed to fetch events:', error);
    throw new Error('Failed to fetch events.');
  }
}
